<template>
	<div id="cart">
		<div class="header-bar">
			<img class="lt-arrow" src="../../assets/arrow.png" @click="$router.go(-1)"/>
			<span class="title">购物车</span>
			<span class="edit" @click="mainEdit">{{editing ? '完成' : '编辑'}}</span>
		</div>
    <template v-if="shops.length>0">
    	<div class="cart-wrap">
	      <!-- 购物车内容 start -->
	      <div class="goods-info" v-for="(item,index) in shopList">
	        <!--店铺信息、编辑 start-->
	        <div class="goods-title" :data-id='item.orgId'>
	          <div class="input-checkbox"
	               :class="{ 'input-checked': item.isChecked}" >
	            <input type="checkbox" @change="checkChange" data-type="shop" :data-id="item.orgId">
	          </div>
	          <span class="label">{{item.directSale == 1 ? '自营' : '加盟' }}</span>
	          <span class="shop-name">{{item.orgName}}</span>
	          <span class="edit" @click="itemEdit(item.orgId)" v-if="!editing">{{editItem[item.orgId]? '完成' : '编辑'}}</span>
	        </div>
	        <!--店铺信息、编辑 end-->
	        <!-- 商品列表  start -->
	        <div class="goods-detail" v-for="(good,index) in item.cartInfos" :data-id="good.id">
	          <div class="check-box">
	            <div v-if='good.isInvalid != 1'
	                 class="input-checkbox"
	                 :class="{ 'input-checked': good.isChecked}" >
	              <input type="checkbox"
	                     @change="checkChange"
	                     data-type="good"
	                     :data-id="good.id"
	                     :data-shop="item.orgId"  >
	            </div>
	            <div class="unused" v-else></div>
	          </div>
	          <div class="good-img">
	            <img class="goods-img" :src='good.imagePath' alt=""/>
	          </div>
	          <!-- 商品信息 start -->
	          <div class="content" v-if='!editing && !editItem[item.orgId]'>
	            <div class="goods-name">{{good.name}}</div>
	            <div class="goods-norms">{{good.productSize}}</div>
	            <div class="goods-prices" v-if='good.isInvalid != 1'>
	              <span class="price">￥{{good.discountPrice}}</span>
	              <span class="num fl-right"><em>x</em>{{good.buyNum}}</span>
	            </div>
	            <div class="del-btn" v-else @click="showDel(good)">删除</div>
	          </div>
	          <!-- 商品信息 end -->
	          <!-- 编辑状态 start -->
	          <div class="content2" v-else>
	            <div class="buy-control" :data-id="good.id" :data-shop="item.orgId">
							<span class="buy-control-sub control"
	                  :class="{'c-ccc': good.buyNum < 2}"
	                  @click="good.buyNum < 2?'':goodCount(item.orgId,good.id,-1,good)"
	            >-</span>
	
	              <input class="but-control-input" type="num" name="" :value="good.buyNum" disabled>
	
	              <span class="buy-control-add control"
	                    :class="{'c-ccc': good.buyNum >= good.inventory}"
	                    @click="good.buyNum >= good.inventory?'':goodCount(item.orgId,good.id,1,good)"
	              >+</span>
	            </div>
	            <div class="goods-norms">{{good.productSize}}</div>
	            <div class="rt-del" @click="showDel(good)">删除</div>
	          </div>
	          <!-- 编辑状态 end -->
	        </div>
	        <!-- 商品列表  end -->
	      </div>
	      <!-- 购物车内容 end -->
	      <!-- 底部bar start -->
	      <div class="footer">
	        <div class="goods-total">
	          <div class="check-box">
	            <div class="input-checkbox"
	                 :class="{ 'input-checked': isSelAll}">
	              <input type="checkbox" @change="checkChange" data-type="all" data-id="0">
	            </div>
	          </div>
	          <span class="total-text">全选</span>
	          <div class="fl-right">
	            <span class="total-price">合计：</span>
	            <span class="price">￥{{getTotalPrice}}</span>
	            <span class="goods-account" @click="linkToConfirmOrder" v-show="!editing">立即结算</span>
	            <span class="goods-account" @click="delAll" v-show="editing">立即删除</span>
	          </div>
	        </div>
	      </div>
    	</div>
	      
	      <!-- 底部bar end -->
    </template>
    
    <template v-else>
      <div class="empty">
        <img src="../../assets/cart/cart-gray.png" class="cart-nothing">
        <div class="cart-nothing-text">购物车空空如也<br>来挑几件好货吧</div>
      </div>
    </template>

	</div>
</template>

