//								packing:{id:0,name:'包装',price:0,packingUnit:0},
//								name: '酷美人 夜光石手链 男女款萤石手链 手串手环饰品2',
//								productSize: '颜色：红色    尺寸：大号',
//								buyNum: 1,
//								originalPrice: 180,
//								discountPrice: 180,
//								imagePath: '/static/img/pic.603b63b.png',
//								inventory: 4,
//								isInvalid: 2,
//							},
//							{
//								id: 7,
//								spuId:7,
//								skuId:7,
//								customId:0,
//								promId:0,
//								packing:{id:0,name:'包装',price:0,packingUnit:0},
//								name: '酷美人 夜光石手链 男女款萤石手链 手串手环饰品2',
//								productSize: '颜色：红色    尺寸：大号',
//								buyNum: 1,
//								originalPrice: 180,
//								discountPrice: 180,
//								imagePath: '/static/img/pic.603b63b.png',
//								inventory: 4,
//								isInvalid: 2,
//							}
//						],
//					}
//				],
        shops:[],
        shopList: [],
				editing: false,
        editItem: {},

        isSelAll: false,
        totalPrice: 0,
        isDel: false,
        isRemind: false,
        
        submitShopList:[],
			}
		},
		created(){
			this.getCartDetails()
		},
    mounted(){

    },
    computed:{
      //计算总价
      getTotalPrice(){
        let total = 0;
        let sl = this.shopList;
        sl.forEach((res) => {
          res.cartInfos.forEach((val) => {
            if(val.isChecked == true && val.isInvalid != 1){
              total += val.buyNum*val.discountPrice
              this.totalPrice = total;
            }
          })
        });
        return total;
      },
    },
    watch: {

    },
    components: {
    	
    },
    methods: {
    	//获取勾选的商品和门店
    	getSubmitShopList(type){
        let sl = this.shopList;
        let orderList = []
    		sl.forEach((res) => {
    			let shopObj = {}
    			let cartObj = {}
    			let num = 0 
    			let cartInfos = []
          res.cartInfos.forEach((val) => {
            if(val.isChecked == true && val.isInvalid != 1){
            	num++;
            	if(num==1){
            		cartInfos.push(val)
            		shopObj = res
	              shopObj = Object.assign({},shopObj,{
			          	cartInfos:cartInfos
			          })
	              orderList.push(shopObj)
            	}else if(num>1){
            		cartInfos.push(val)
            		shopObj = Object.assign({},shopObj,{
			          	cartInfos:cartInfos
			          })
	              orderList.pop()
	              orderList.push(shopObj)
            	}
            }
          })
        });
        this.submitShopList = orderList
      	if(type == 'add'){
        		localStorage.removeItem('orderList')
        		let str = JSON.stringify(orderList)
        		localStorage.setItem('orderList',str)
    		}
      	if(type == 'del'){
      		return orderList
      	}
    	},
      /*编辑状态 start*/
      //编辑全部
			mainEdit(e) {
				this.editing = !this.editing
        if(this.editing)
          this.editItem = {}
			},
      //按店铺编辑
			itemEdit(id) {
        let list = this.editItem;
        let bool = list[id] || false;
        this.$set(this.editItem,id,!bool);
			},
      goodCount(shop,id,type,item){
      	console.log(item)
      	let sl = this.shopList
      	let selId = id
        let selShop = shop
        sl.forEach((res) => {
          if(selShop == res.orgId ){
            res.cartInfos.forEach((val) => {
              if(selId == val.id ){
                let num = val.buyNum;
                num = num+type;
                this.$set(val,'buyNum',num);
              }
            })
          }
        })
        let params = {
        	skuId : item.skuId,
        	spuId : item.spuId,
        	num : 1,
        	promId : item.promId,
        	packingId : item.packingId
        }
        //修改购物车数量
	      if(type == 1){
	      	this.getCartUpdate(1,params)
	      }else if(type == -1){
	      	this.getCartUpdate(2,params)
	      }
      },
      /*编辑状态 end*/
      /*购物车选择 start*/
      //判断选择类型【选商品，选店铺，全选】
			checkChange(e) {
//				console.log(e.target.dataset)
        let type = e.target.dataset.type
        switch (type){
          case 'all':
            this.selAll()
            break;
          case 'shop':
            this.selShop(e.target.dataset.id)
            break;
          case 'good':
            this.selGood(e.target.dataset.shop,e.target.dataset.id)
            break;
        }
			},
      //全选
      selAll(){
        //console.log('全选');
        let sl = this.shopList;
        this.isSelAll = !this.isSelAll;
        sl.forEach((res) => {
          this.$set(res,'isChecked', this.isSelAll);
          res.cartInfos.forEach((val) => {
            this.$set(val,'isChecked', this.isSelAll);
          })
        })

      },
      //选店铺
      selShop(id){
        //console.log('店铺id：'+id);
        let selShop = id,
            sl = this.shopList;
        sl.forEach((res) => {
          if(selShop == res.orgId ){
            let isTrue = !res.isChecked
            this.$set(res,'isChecked',isTrue);
            res.cartInfos.forEach((val) => {
              this.$set(val,'isChecked',isTrue);
            })
            this.isAll();
          }
        })
      },
      //选商品
      selGood(shop,id){
        //console.log('店铺id：'+shop);
        //console.log('商品id：'+id);
        let sl = this.shopList,
          selShop = shop,
          selId = id;
        sl.forEach((res) => {
          if(selShop == res.orgId ){
            res.cartInfos.forEach((val) => {
              if(selId == val.id ){
                let isTrue = !val.isChecked
                this.$set(val,'isChecked',isTrue);
                this.isShopAll(shop);
                this.isAll();
              }
            })
          }
        })
      },
      //初始化购物车列表，添加isChecked[Boolean]属性
      getShopList(data){
      	this.shopList = []
        data.forEach((res) => {
          this.$set(res,'isChecked',false);
          res.cartInfos.forEach((val) => {
            if(val.isInvalid != 1)
              this.$set(val,'isChecked',false);
          })
          this.shopList.push(res);

        })
      },
      //判断店铺中的商品是否全部被选
      isShopAll(id){
        let selShop = id,
          sl = this.shopList;

        sl.forEach((res) => {
          if(selShop == res.orgId ){
            this.$set(res,'isChecked',true);
            res.cartInfos.forEach((val) => {
              if(!val.isChecked && val.isInvalid != 1){
                this.$set(res,'isChecked',false);
                return;
              }
            })
          }
        })
      },
      //判断购物车的商品是否全部被选
      isAll(){
        let sl = this.shopList;
        this.isSelAll = true;
        sl.forEach((res) => {
          res.cartInfos.forEach((val) => {
            if(!val.isChecked && val.isInvalid != 1){
              this.isSelAll = false;
              return;
            }
          })
        })
      },
      /*购物车选择 end*/
      showDel(good){
				console.log(good)
				let goodInfo = good
        this.$popup({
          content:'确定要删除选中的商品吗？'
        }).then((res) => {
          if(res == 'sure'){
						console.log(goodInfo)
          	let params = {
          		skuId:goodInfo.skuId,
          		spuId:goodInfo.spuId,
          		num:0,
          	}
          	this.getCartUpdate(2,params)
          }
        })
      },
      showDelPopup(isTrue){
        this.isDel = isTrue;
        console.log(this.isDel)
      },
      showRemind(isTrue){
        this.isRemind = !isTrue;
      },
      linkToConfirmOrder(){
      	this.getSubmitShopList('add')
      	if(this.submitShopList.length>0){
      		this.$router.push({name: 'ConfirmOrder'})
      	}else{
      		this.$toast('请勾选购买的商品')
      	}
      },
      delAll(){
        this.$popup({
          content:'确定要删除选中的商品吗？'
        }).then((res) => {
          console.log(res)
          if(res=='sure'){
          	let delArr = this.getSubmitShopList('del')
          	let goodsList = []
          	delArr.forEach((item)=>{
          		let goods = {}
          		item.cartInfos.forEach((res)=>{
          				goods.skuId = res.skuId
          				goods.spuId = res.spuId
          				goods.num = 0
          				goods.promId = res.promId
          				goods.packingId = res.packingId
          		})
          		goodsList.push(goods)
          	})
          	console.log(goodsList)
          	this.getCartUpdate(2,goodsList)
          }
        })
      },
      //接口
      getCartDetails(){
      	this.$Api.getCartDetails(1).then((res)=>{
      		console.log(res)
      		if(res.q.s == 0){
      			this.shops = res.q.shops
      			this.getShopList(res.q.shops)
      			
      		}
      	})
      },
      getCartUpdate(action,params){
      	let goodsList = []
      	goodsList.push(params)
				this.$Api.getCartUpdate(action,goodsList).then((res)=>{
					if(res.q.s==0){
						//删除商品才调用
						if(params.num === 0){
							this.getCartDetails()
						}
					}else{
						this.$toast(res.q.d);
					}
				})
			},
		}
	}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#cart {
    position: relative;
		height: auto;
		min-height: 100%;
		background: #f0f0f0;
		/*头部样式*/
		.header-bar {
			/*display: flex;*/
			position: fixed;
      top: 0;
      width: 100%;
      z-index: 99;
      height: 1.17rem;
      line-height: 1.17rem;
      /*align-items: center;*/
      padding: 0 0.4rem;
			text-align: center;
			color: #fff;
			@include bg-color;
			.lt-arrow {
				/*@include lt-arrow;*/
				display: inline-block;
				@include lt-arrow;
			}
			.title {
				font-size: 0.48rem;
			}
			.edit {
				position: absolute;
				right: 0.4rem;
				font-size: 0.4rem;
			}
		}
    .cart-wrap{
      position: absolute;
      top: 1.17rem;
      bottom: 1.31rem;
      width: 100%;
      overflow-y: auto;
    }
		.goods-info {
			background: #fff;
			margin-bottom: 0.27rem;
			.goods-title {
				position: relative;
				display: flex;
				align-items: center;
				font-size: 0.37rem;
				height: 1.07rem;
				padding: 0 0.4rem;
				border-bottom: 1px solid $border-color;
				>span {
					display: inline-block;
				}
				.label {
					color: $label-color;
				}
				.shop-name {
					margin-left: 0.27rem;
				}
				.edit {
					position: absolute;
					right: 0.4rem;
				}
				.shop-name,
				.edit {
					color: $text-color;
				}
			}
			.goods-detail {
				position: relative;
				display: flex;
				height: 2.67rem;
				padding: 0.21rem 0.4rem;
				border-bottom: 1px solid $border-color;
				.check-box {
					width: 0.8rem;
					height: 2.13rem;
          position: relative;
					line-height: 2.13rem;
					.unused {
						display: inline-block;
            width: 0.56rem;
            height: 0.43rem;
            background: url("../../assets/cart/ico_invalid.png") no-repeat center;
            background-size: 100% 100%;
					}
          .input-checkbox{
            position: absolute;
            top: 50%;
            left: 0;
            margin-top: -0.28rem;
          }
				}
				.good-img {
					width: 2.13rem;
					height: 2.13rem;
				}
				.goods-img {
					width: 2.13rem;
					height: 2.13rem;
					border-radius: 0.11rem;
				}
				.content {
					flex: 1;
					margin-left: 0.35rem;
					display: flex;
					justify-content: space-around;
					flex-direction: column;
					.goods-name {
						font-size: 0.37rem;
						color: $text-color;
						@include textflow-two;
					}
					.goods-norms {
						color: $text-color2;
						font-size: 0.32rem;
						@include textflow-one;
					}
					.goods-prices {
						.price {
							color: $price-color;
							font-size: 0.43rem;
							.price-icon {
								font-size: 0.32rem;
							}
						}
						.num {
							color: $text-color2;
							font-size: 0.32rem;
							>em {
								font-size: 0.24rem;
							}
						}
					}
					.del-btn {
						font-size: 0.32rem;
						width: 1.33rem;
						height: 0.53rem;
						border-radius: 0.07rem;
						color: $price-color;
						border: 1px solid $price-color;
						text-align: center;
					}
				}
				.content2 {
					margin-left: 0.35rem;
					.buy-control {
            color: #333;
						font-size: 0;
						.control {
							display: inline-block;
							width: 1.17rem;
							height: 1.17rem;
							line-height: 1.17rem;
							text-align: center;
							font-size: 0.67rem;
							border: 1px solid $border-color;
						}
            .c-ccc{
              color: #ccc;
            }
						.buy-control-sub {
							border-right: 0;
						}
						.buy-control-add {
							border-left: 0;
						}
						.but-control-input {
							font-size: 0.53rem;
							width: 1.87rem;
							height: 1.17rem;
							line-height: 1.17rem;
							vertical-align: top;
							text-align: center;
							border: 1px solid $border-color;
						}
					}
					.goods-norms {
						color: $text-color2;
						font-size: 0.32rem;
						@include textflow-one;
						margin-top: 0.21rem;
					}
					.rt-del {
						position: absolute;
						right: 0;
						top: 0;
						font-size: 0.4rem;
						width: 1.6rem;
						height: 2.67rem;
						line-height: 2.67rem;
						background: #fe4436;
						color: #fff;
						text-align: center;
            border-bottom: 1px solid $border-color;
					}
				}
			}
		}
		.footer {
			position: fixed;
			right: 0;
			bottom: 0;
			left: 0;
			background: #fff;
			height: 1.31rem;
			line-height: 1.31rem;
			.goods-total {
				font-size: 0;
				height: 1.31rem;
				line-height: 1.31rem;
				width: 100%;
				padding-left: 0.4rem;
				.check-box {
					display: inline-block;
					width: 0.8rem;
					height: 1.31rem;
          position: relative;
					line-height: 1.31rem;
          .input-checkbox{
            position: absolute;
            top: 50%;
            /*left: 50%;*/
            margin-top: -0.28rem;
            /*margin-left: -0.28rem;*/
          }
				}
				.total-text {
					display: inline-block;
					height: 1.31rem;
					line-height: 1.31rem;
					font-size: 0.37rem;
					color: $text-color;
					vertical-align: top;
				}
				.total-price {
					font-size: 0.4rem;
					color: $text-color;
					margin-right: 0.27rem;
				}
				.price {
					color: $price-color;
					font-size: 0.4rem;
					margin-right: 0.4rem;
				}
				.goods-account {
					display: inline-block;
					font-size: 0.43rem;
					color: #fff;
					@include bg-color;
					width: 2.91rem;
					height: 1.31rem;
					line-height: 1.31rem;
					text-align: center;
				}
			}
		}
    .input-checkbox{
      width: box(40);
      height: box(40);
      position: relative;
      margin-right: 0.31rem;
      border: solid box(2) #999999;
      border-radius: 100%;

      input{
        width: 100%;
        height: 100%;
        filter:alpha(opacity=0);
        -moz-opacity:0;
        -khtml-opacity: 0;
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    .input-checked{
      width: box(40);
      height: box(40);
      background: url("../../assets/cart/ico_checked.png") no-repeat center;
      background-size: 100% 100%;
      border: none;
    }
    .empty{
      position: relative;
      width: 100%;
      top: 2.13rem;
      left: 50%;
      transform: translateX(-50%);
    }
    .cart-nothing{
      width: 3.33rem;
      height: 3.33rem;
      margin:0 auto;
      display: block;

    }
    .cart-nothing-text{
      font-size: 0.4rem;
      line-height: 0.53rem;
      color: #666;
      text-align: center;
      margin: 0.4rem auto;
    }
	}
</style>



